	methods: {
			previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: [e]
			})
		},
			getInfo:function(){
			var _self = this
			 uni.request({
			 	url:_self.$api+"dockingManager/stationedQuery",
				data:{id:_self.id,name:"",pull:0}, 
				success:function(res){
					console.log(res)
					_self.List = res.data[0]
					_self.upper_id = res.data[0].upper_id
					_self.Id = res.data[0].Id
					console.log(_self.upper_id)
				}
			 })
		},
		/**
		 * 判断是否验证
		 */
		checkAuth:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/judgeCard",
				data:{optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					if(res.data==0){
						_self.checkTitle = false
					}
					if(res.data==1){
						_self.checkTitle = true
					}
				}
			})
			
